#progressScroll
{
    max-height: 50px;
    width: max-content;
    overflow-y: auto;
    background-color: rgb(227, 194, 233);
    border-radius: 9px;
    direction: rtl;
    font-size: 1.1rem;
    font-style: italic;

}
body {
    background: radial-gradient(#fcd7c2, #ffff, #fcd7c2);
  }
  
  #placeName {
    direction: rtl;
