  }
  
  .card {
    width: 20rem;
    height: 25rem;
    margin-top: 2rem;
    background: #fff3e6
  }
  
  .card:hover {
    box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.253);
  }
  .card-body {
