    /* padding: 0.4rem 0.5rem; */
  }
  
  #StoriesCards {
    /* padding-top: 6rem; */
    /* margin-top: 2rem; */
    margin-bottom: 5rem;
  }
  .overflow {
    overflow: hidden;
  }
  .card-img-top {
    /* transform: scale(1);
    transition: transform 0.5s ease; */
    height: 10rem;
    /* width: inherit; */
  }
  
